
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Popup from 'vux/src/components/popup/index.vue'
import Checker from 'vux/src/components/checker/checker.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import XImg from 'vux/src/components/x-img/index.vue'
import Confirm from 'vux/src/components/confirm/index.vue'
import Countup from 'vux/src/components/countup/index.vue'

export default {
name: 'account',
directives: {
TransferDom
},
components:{
Cell, Group,Flexbox, FlexboxItem,Popup,Checker, CheckerItem,XImg,Confirm,Countup
},
data(){
return{
confirmContent:'您尚未完成任务，无法提现，是否前往完成任务？',
totalMoney:1212.12,
zbMoney:'1212.12',
listStatus:false,
typeStatus:false,
confirmStatus:false,
type:'1',
imgSrc:require('@/assets/images/card-img.png'),
defaultImgSrc:require('@/assets/images/default.jpg'),
}
},
filters:{
typeFilter:(value) => {
switch (value) {
case '1':
return '全部';
case '2':
return '直播';
case '3':
return '礼物';
case '4':
return '分享';
case '5':
return '提现';
}
}
},
methods:{
openType(){
this.typeStatus = true;
},
closeType(){
this.typeStatus = false;
},
choseType(){
console.log(this.type)
this.typeStatus = false;
},
success (src, ele) {
console.log('success load', src)
},
error (src, ele, msg) {
console.log('error load', msg, src)
},
goPage(){
this.confirmStatus = true
},
onHide () {
console.log('on hide')
},
onShow () {
console.log('on show')
},
onCancel () {
console.log('on cancel')
},
onConfirm () {
console.log('on confirm')
this.$router.push({
name:'cashOut'
})
}
},
created () {

}
}



